<script>

  import { updateRestaurantInfo, getRestaurantInfo } from '@/components/Utlis/api'
  import Utils from '../../components/Utlis/Utils'
  import GlobalSettings from '@/GlobalSettings'
  import store from '../../store'
  import hillo from 'hillo'

  const config = require('@/assets/config.json')

  export default {
    data: function () {
      return ({
        modifyPsw: '',
        dialog: null,
        bonLogoDialog: null,
        buffetLogoDialog: null,
        uploadBonLogoFile: null,
        uploadBuffetLogoFile: null,
        modifyRight: true,
        noteShow: '',
        statusList: [{ id: '0', name: this.$i18n.t('YES') }, { id: '1', name: this.$i18n.t('NO') }],
        languageList: [{ id: '0', name: this.$i18n.t('zh') }, { id: '1', name: this.$i18n.t('en') }, { id: '2', name: this.$i18n.t('de') }],
        statusItem: {
          id: '',
          name: '',
        },
        langItem: {
          id: '',
          name: '',
        },
        loading: true,
        search: '',
        desserts: [],
        resinfo: {},
        editedIndex: -1,
        takeawayOption: [
          {
            id: '0',
            name: 'Close',
          },
          {
            id: '1',
            name: 'Open',
          },
        ],
        externalLetter: '',
        editedItem: {
          name: '',
          adress1: '',
          adress2: '',
          postCode: '',
          city: '',
          state: '',
          taxNumber: '',
          telephone: '',
          latitude: '',
          longitude: '',
          inhalterName: '',
          inhalterLastName: '',
          inhalterTitle: '',
          emailAddress: '',
          totalRound: '',
          maxDineTime: '',
          roundTime: '',
          roundDishCount: '',
          takeawayPriceModification: '',
          childRoundDishCount: '',
          tableColor: '',
          callColor: '',
          bonLogo: '',
          buffetLogo: '',
          currentlyOpening: '',
          letterOnExternalOrder: '',
        },
        tableColorDisplay: '',
        tableColorMenu: false,
        callColorDisplay: '',
        callColorMenu: false,
        closeTakeAway: ['False', 'True'],
        RInfoAdvertisement: '',
      })
    },

    computed: {
      bonLogo: function () {
        return this.editedItem.bonLogo
      },
      buffetLogo: function () {
        return this.editedItem.buffetLogo
      },
      swatchStyle () {
        const { tableColorDisplay, tableColorMenu } = this
        return {
          backgroundColor: tableColorDisplay,
          cursor: 'pointer',
          height: '30px',
          width: '30px',
          borderStyle: 'solid',
          borderColor: '#c1c1c1',
          borderWidth: '1px',
          borderRadius: tableColorMenu ? '50%' : '4px',
          transition: 'border-radius 200ms ease-in-out',
        }
      },
      swatchStyleCall () {
        const { callColorDisplay, callColorMenu } = this
        return {
          backgroundColor: callColorDisplay,
          cursor: 'pointer',
          height: '30px',
          width: '30px',
          borderStyle: 'solid',
          borderColor: '#c1c1c1',
          borderWidth: '1px',
          borderRadius: callColorMenu ? '50%' : '4px',
          transition: 'border-radius 200ms ease-in-out',
        }
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
    },

    created () {
      this.initialize()
    },

    methods: {
      processFile (event) {
        this.uploadBonLogoFile = event
        this.editedItem.bonLogo = URL.createObjectURL(event)
      },

      processFileBuffet (event) {
        this.uploadBuffetLogoFile = event
        this.editedItem.buffetLogo = URL.createObjectURL(event)
      },

      async bonLogoSave () {
        await hillo.postWithUploadFile('Restaurant.php?op=changeBonLogo', {
          file: this.uploadBonLogoFile,
        }, { showLoading: true })
        this.bonLogoDialog = false
      },

      async buffetLogoSave () {
        await hillo.postWithUploadFile('Restaurant.php?op=changeBuffetLogo', {
          file: this.uploadBuffetLogoFile,
        }, { showLoading: true })
        this.buffetLogoDialog = false
      },

      async initialize () {
        const res = await getRestaurantInfo()
        this.editedItem = res.content[0]
        console.log(this.editedItem)
        this.statusItem.id = GlobalSettings.isShowOnTable
        for (const i of this.languageList) {
          if (i.name === this.$i18n.t(localStorage.getItem('lang'))) {
            this.langItem.id = i.id
          }
        }

        this.tableColorDisplay = this.editedItem.tableColor
        this.callColorDisplay = this.editedItem.callColor
        if (this.editedItem.bonLogo) {
          const tmpArr = this.editedItem.bonLogo.split('/../')
          this.editedItem.bonLogo = store.getters.rootUrl + tmpArr[1]
        }

        if (this.editedItem.buffetLogo) {
          this.editedItem.buffetLogo = store.getters.rootUrl + this.editedItem.buffetLogo
        }
      },
      summit () {
        this.editedItem.tableColor = this.tableColorDisplay
        this.editedItem.callColor = this.callColorDisplay
        Utils.showConfirm('Are you sure?', 'you want to summit', () => {
          updateRestaurantInfo(this.editedItem).then(() => {
            Utils.toast('修改成功')
            this.initialize()
          })
        })
      },
      selectStatus (i) {
        if (i === '0') {
          localStorage.setItem('isShowOnTable', '0')
        } else {
          localStorage.setItem('isShowOnTable', '1')
        }
        this.$router.go(0)
      },
      selectLangs (i) {
        this.$i18n.locale = config.supportLang[i].lang
        localStorage.setItem('lang', this.$i18n.locale)
        this.$router.go(0)
      },
      close () {
        this.dialog = false
        this.editedIndex = -1
      },
      pswConfirm () {
        console.log(store.state.modifyClientInfoPsw, 'config.modifyClientInfoPsw ')
        if (this.modifyPsw === store.state.modifyClientInfoPsw) {
          this.modifyRight = false
          this.dialog = !this.dialog
          this.noteShow = ''
        } else {
          this.noteShow = 'Das Passwort ist inkorrekt!'
        }
      },
    },
  }
</script>
